import React from 'react';
import NavBar from "../../../navbar/feeds-top-navbar/feeds-top-navbar.components"
import './sent-interview-requests.styles.css'
import SentInterviewRequestsComponentComponent from "./sent-interview-requests-component.component";

const SentInterviewRequestsComponent = ({ requests }) => {
  return (
    <div>
      <NavBar />
      <div className="content">
        <div className="first-card-body">
          <div className="header-3">
            <h3>Sent Request</h3>
          </div>
        </div>

          {
              requests.map(request => (
                  <SentInterviewRequestsComponentComponent key={request._id} request={request}/>
              ))
          }
      </div>
    </div>
  );
};

export default SentInterviewRequestsComponent;
