import axios from "axios";
import Api from "./Api";

class Booking extends Api{


    createTalentBooking = async (receiverId) => {
        return await axios.post(`${this.endPoint()}/bookings/${receiverId}`,{},{ withCredentials: true });
    }

    isTalentBooked = async (talentId) => {
        return await axios.get(`${this.endPoint()}/bookings/is-player-booked/${talentId}`,{ withCredentials: true });
    }

    fetchRequests = async (talentId) => {
        return await axios.get(`${this.endPoint()}/bookings?receiver=${talentId}`,{ withCredentials: true });
    }

    fetchCoachRequests = async (coachId) => {
        return await axios.get(`${this.endPoint()}/bookings?user=${coachId}`,{ withCredentials: true });
    }


    updateRequestStatus = async (coachId, status) => {
        return await axios.patch(`${this.endPoint()}/bookings/booking/update-status/${coachId}`,{
            status
        },{ withCredentials: true });
    }
}

export default new Booking;