import React, {useState} from 'react';
import { Container, Card, Button, Form, Navbar } from "react-bootstrap";
import ButtonSpinner from "../../../spinners/button-spinner.component";
import System from "../../../../backend/System";
import Booking from "../../../../backend/Booking";

// SweetAlert
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
const notify = withReactContent(Swal);

const InterviewRequestsComponentComponent = ({ request }) => {
    console.log(request);
  const [requestStatus, setRequestStatus] = useState(false);
  const [loading, setLoading] = useState(false);

  const accept = async () => {
      await setLoading(true);
      await Booking.updateRequestStatus(request.user._id, 'Accepted').then(response => {
          console.log(response.data);
          setRequestStatus(true);
         setLoading(false);
          notify.fire({
              icon: 'success',
              title: 'Request accepted',
              showCancelButton: false,
              confirmButtonColor: '#3085d6',
              confirmButtonText: 'Ok',
          });
        //set request stat  us to true upon completion
      }).catch(async error => {
        await setLoading(false);
        //set loading false
      });
  }

  const reject = async () => {
      await setLoading(true);
      await Booking.updateRequestStatus(request.user._id, 'Rejected').then(response => {
          setRequestStatus(true);
          setLoading(false);
          notify.fire({
              icon: 'success',
              title: 'request rejected',
              showCancelButton: false,
              confirmButtonColor: '#3085d6',
              confirmButtonText: 'Ok',
          });
          //set request stat  us to true upon completion
      }).catch(async error => {
          await setLoading(false);
          //set loading false
      });
  }

  return (
      <Card className="interview-card">
        <Card.Body className="interview-card-body">
          <img src={System.userImagePath(request.user.image)} className="interview-image" alt="" />
          <div className="interview-body">
            <Card.Title>{request.user.name}</Card.Title>
            <Card.Text style={{ color: "#979696", fontSize: "15px" }}>
              Hey there, saw your profile and quite impressive. Lets meet to
              discuss a few things on joining our team.
              Can we schedule a call on Zoom for discuss our terms.?
            </Card.Text>
              {
                  (request.status === 'Pending' || requestStatus === true)? <div>
                      <Button
                          onClick={accept}
                          disabled={loading}
                          variant="outline-success"
                          type="submit"
                          className="btn interview-btn"
                          style={{ marginLeft: "0", marginTop: "0" }}
                      >
                          {loading?'....': 'Accept'}
                      </Button>
                      <Button
                          onClick={reject}
                          disabled={loading}
                          variant="outline-success"
                          type="submit"
                          className="btn interview-btn"
                          style={{ marginLeft: "1rem", marginTop: "0" }}
                      >
                          {loading?'....': 'Decline'}
                      </Button>
                  </div> :  <div>
                      <Button
                          disabled
                          variant="outline-success"
                          type="submit"
                          className="btn interview-btn"
                          style={{ marginLeft: "1rem", marginTop: "0" }}
                      >
                          {request.status}
                      </Button>
                  </div>
              }

          </div>
        </Card.Body>
      </Card>
  );
};

export default InterviewRequestsComponentComponent;
