import React, {useEffect, useState} from "react";
import { Row, Container, Col, Button, Image } from "react-bootstrap";
import './talent-profile.styles.css'
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {createStructuredSelector} from "reselect";
import System from "../../../../backend/System";
import {signOutStart} from "../../../../redux/user/user.actions";
import UserCreatedPostsComponent from "../../../feeds/user-created-posts/user-created-posts.component";
import AboutUserComponent from "../../../profile/about-player/about-player.component";
import {bookTalentStart} from "../../../../redux/booking/booking.actions";
import {selectBookedUser, selectBookingLoadingStatus} from "../../../../redux/booking/booking.selectors";
import ButtonSpinner from "../../../spinners/button-spinner.component";
import Booking from "../../../../backend/Booking";
import history from "../../../../history";

const TalentProfileComponent = ({ player, isLoading, match, bookTalentStart  }) => {
    const [bookingStatus, setBookingStatus] = useState(false);
    const [userPosts, setUserPosts] = useState(true);
    const [aboutUser, setAboutUser] = useState(false);
    const { user } = player;

    useEffect(async () =>{
           await checkIfPlayerBooked();
    },[match.params.player_id]);

    const checkIfPlayerBooked = async () => {
        await Booking.isTalentBooked(match.params.player_id).then(async response =>{
           await setBookingStatus(response.data.status);
        }).catch(error=> {
            console.log(error.response);
            history.push('/talent-pool');
        });
    }

    const bookTalent = async () => {
        bookTalentStart({receiverId: match.params.player_id});
    }


    const showUserPosts = () => {
        setUserPosts(true);
        setAboutUser(false);
    }

    const showAboutUser = () => {
        setAboutUser(true);
        setUserPosts(false);
    }
    const logout = () => {
        signOutStart()
    }

    return (
        <div className="profile">
            <div>
                <div className="player">
                    <Container className="profile-container" fluid>
                        <div className="coachheader">
                            <Image src={System.userImagePath(user.image)} className="coachimg" style={{ maxWidth: "15%" }} />
                            {
                                (!bookingStatus)?  <Button
                                    className="profile-btn"
                                    variant="outline-success custom"
                                    onClick={bookTalent}
                                >
                                    {isLoading?(<ButtonSpinner />): 'Book'}
                                </Button> :
                                    <Button
                                        disabled
                                        className="profile-btn"
                                        variant="outline-success custom"
                                    >
                                        Booked
                                    </Button>
                            }

                        </div>
                    </Container>
                </div>
                <div className="profile-content">
                    <Container className="profile-container">
                        <Row>
                            <Col>
                                <h2>{user.name.toUpperCase()}</h2>
                                <p>
                                    Talented Football Player, favorite Wing:
                                    <span style={{ display: "block" }}>
                           {player.favoriteWingNo}. Born, {player.dateOfBirth} in {player.state}
                            </span>
                                    {player.country}
                                </p>
                            </Col>
                        </Row>
                    </Container>
                    <hr
                        style={{
                            borderTop: "1px solid #747373",
                        }}
                    />
                    <Container className="posts">
                        <Row>
                            <Col>
                                <a href="#" className="post">
                                    <h3 onClick={() => showUserPosts()}>Posts</h3>
                                </a>
                            </Col>
                            <Col>
                                <a href="#" className="post">
                                    <h3 onClick={() => showAboutUser()}>About</h3>
                                </a>
                            </Col>
                            <Col>
                                <Link to="/talent-pool" className="post">
                                    <h3>Talent pool</h3>
                                </Link>
                            </Col>
                        </Row>
                        <Row>
                            {
                                (userPosts)?<UserCreatedPostsComponent userId={user._id} />:null
                            }
                            {
                                (aboutUser)?<AboutUserComponent />:null
                            }
                        </Row>
                    </Container>
                </div>
            </div>
        </div>
    );
}

const mapDispatchToProps = dispatch => ({
    bookTalentStart: receiverId => dispatch(bookTalentStart(receiverId)),
});
const mapStateToProps = createStructuredSelector({
    isLoading: selectBookingLoadingStatus,
    bookedUser: selectBookedUser
});
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TalentProfileComponent));