import React, {useEffect, useState} from 'react';
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import InterviewRequestsComponent from "../../../components/booking/player/requests/interview-requests.component";
import SpinnerComponent from "../../../components/spinners/page-spinner-component/spinner.component";
import Booking from "../../../backend/Booking";
import {selectCurrentUser} from "../../../redux/user/user.selectors";

const InterviewRequestsPage = ({ currentUser }) => {
    const [requests, setRequests] = useState(null);
    useEffect(() => {
        if (requests === null){
            fetchMyRequests();
        }
    }, []);

    const fetchMyRequests = async () => {
        await Booking.fetchRequests(currentUser.user._id).then(response => {
            setRequests(response.data.data.data);
        }).catch(error => {
            console.log(error.response);
        })
    }

  return (
      <div>
          {
              (requests)? <InterviewRequestsComponent requests={requests} /> : <SpinnerComponent />
          }

      </div>
  )
};

const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
})
export default connect(mapStateToProps)(InterviewRequestsPage);
