import React from 'react';
import { Container, Card, Button, Form, Navbar } from "react-bootstrap";
import coachimg from "../../../../assets/img/coachimg.png";
import NavBar from "../../../navbar/feeds-top-navbar/feeds-top-navbar.components"
import './interview-requests.styles.css'
import InterviewRequestsComponentComponent from "./interview-requests-component.component";

const InterviewRequestsComponent = ({ requests }) => {
  return (
    <div>
      <NavBar />
      <div className="content">
        <div className="first-card-body">
          <div className="header-3">
            <h3>Interview Request</h3>
          </div>
        </div>

          {
              requests.map(request => (
                  <InterviewRequestsComponentComponent key={request._id} request={request}/>
              ))
          }
      </div>
    </div>
  );
};

export default InterviewRequestsComponent;
