import React from "react";
import {Button, Card} from "react-bootstrap";
import history from "../../../../history";
import System from "../../../../backend/System";

const TalentComponentComponent = ({ talent }) => {
    console.log(talent);
    return (
        <Card className="interview-card">
            <Card.Body className="interview-card-body">
                <img src={System.userImagePath(talent.user.image)} alt="" className="interview-image" />
                <div className="interview-body">
                    <Card.Title><b>{talent.favoriteWingNo}</b> {talent.user.name}</Card.Title>
                    <Card.Text>
                        <div style={{ color: "#979696", fontSize: "15px" }}>
                            Favorite Wing: {talent.favoriteWingNo}. Born, {talent.dateOfBirth} in {talent.state} state, {talent.nationality}
                            <span style={{ display: "block", fontSize: "80%" }}>
                      Click view to check profile...
                    </span>
                        </div>
                    </Card.Text>
                </div>
                <div>
                    <Button
                        className="interview-btn"
                        variant="outline-success"
                        type="submit"
                        onClick={() => history.push(`/talent/${talent._id}`) }
                    >
                        View
                    </Button>
                </div>
            </Card.Body>
        </Card>
    )
}

export default TalentComponentComponent;